import { String } from "../../Model/String";
import { Injectable } from "@angular/core";
import { ServicesProvider } from "../services/services";
import { FordListResponse } from "../../Model/Response/FordListResponse";
import { FordResponse } from "../../Model/Response/FordResponse";
import { FordViewModel } from '../../Model/ViewModel/FordViewModel';
import { StringViewModel } from "../../Model/ViewModel/StringViewModel";
import { TableSetServicesProvider } from '../table-set-services/table-set-services';
/*
  Generated class for the FordServiceProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/

@Injectable()
export class FordServicesProvider  extends TableSetServicesProvider {
  private Fords: FordListResponse;
  private Ford: FordResponse;
   ctl: string;

  constructor(
  public Services: ServicesProvider
  ) {
    super(Services);
    this.ctl = "Ford";
    console.log("Hello FordServiceProvider Provider");
  }

  public GetListsByAsync(PageSize: number, PageNumber: number, keyword: string,order_type:boolean) {
    var sub_url: string;
     sub_url = "Ford?PageSize={1}&PageNumber={2}&keyword={3}&order_type={4}";
    sub_url = String.Format(sub_url, this.ctl, PageSize, PageNumber, keyword,order_type);
    return this.Services.GetAsync(sub_url,this.ctl,true).map(item => {
      this.Fords = new FordListResponse({
        Message: item.Message,
        DidError: item.DidError,
        ErrorMessage: item.ErrorMessage,
        PageSize: item.PageSize,
        PageNumber: item.PageNumber,
        TotalRows: item.TotalRows,
        PageRows: item.PageRows,
        TotalPages: item.TotalPages,
        Model: item.Model
      });
      return this.Fords;
    });
  }



  public PostPutAsync(FordViewModel:FordViewModel,mode:string) {
    var sub_url: string;
    if(mode==="POST")
      sub_url = "Ford";
    else
    sub_url = "Ford/{1}";
    sub_url = String.Format(sub_url, this.ctl,FordViewModel.exec_table_seq);
    return this.Services.PostPutAsync(sub_url, this.ctl, true,FordViewModel,mode).map(
      item => {
        this.Ford = new FordResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });

        return this.Ford;
      }
    );
  }

  public ExecuteAsync(StringViewModel:StringViewModel) {
    var sub_url: string;
    sub_url = "Ford/Execute";
    return this.Services.PostAsync(sub_url, this.ctl, true,StringViewModel).map(
      item => {
        this.Ford = new FordResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });

        return this.Ford;
      }
    );
  }


  public DeleteAsync(exec_table_seq:string) {
    var sub_url: string;
    sub_url = "Ford/{1}";
    sub_url = String.Format(sub_url, this.ctl,exec_table_seq);

    return this.Services.DeleteAsync(sub_url, this.ctl, true).map(
      item => {
        this.Ford = new FordResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });
        return this.Ford;
      }
    );
  }





}



